import Vue from 'vue'
import ElementUI from 'element-ui'
import './styles/theme/index.css';
import App from './App'
import router from './router'
import store from './store'
import '@/permission' // 用户登录鉴权 路由守卫


Vue.config.productionTip = false
Vue.use(ElementUI)

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})




