import fetch from './fetch'

//==========公共接口==========
// 用户登录
export const login = (formData) => {
  return fetch({
    url: '/_house_api/login',
    method: 'post',
    data: formData
  })
}
// 获取用户信息
export const getInfo = () => {
  return fetch({
    url: '/_house_api/getUserInfo',
    method: 'post',
  })
}
// 注册用户
export function register(formData) {
  return fetch({
    url: '/_house_api/register',
    method: 'post',
    data: formData
  })
}



