import Vue from 'vue'
import Router from 'vue-router'
// import store  from '../store'
import Layout from '../views/layout/Layout'
Vue.use(Router)

//路由引入模块 懒加载
const _import = file => resolve => require(['@/views/'+file+'.vue'], resolve);

const router = new Router({
  routes: [
    { path: '/login', component: _import('login/login'), hidden: true },
    { path: '/register', component: _import('login/register'), hidden: true },
    {
      path: '/',
      name:'Home',
      // redirect: '/login'
      redirect: '/control/index'
    },
    {
      path: '/control',
      component: Layout,
      children: [{
        path: 'index',
        component: _import("control/index"),
      }, {
        path: 'api',
        name: 'ApiData',
        component: _import("control/apiList"),
      }, {
        path: 'backups',
        name: 'Backups',
        component: _import("control/apiInfo"),
      }, {
        path: 'userinfo',
        name: 'userinfo',
        component: _import("control/userInfo"),
      }]
    },
    { path: '/404', component: _import('error/404'), hidden: true },
    { path: '*', redirect: '/404', hidden: true }
  ]
})


export default router


