import Vue from 'vue'
import Vuex from 'vuex'
// import user from './modules/user'
import getters from './getters'
import { login, getInfo } from '@/api'
import { getToken, setToken, removeToken } from '@/utils/auth'
import {Message} from 'element-ui'

Vue.use(Vuex)

const state = {
  userInfo:null,
  activeHost:'',//当前域名
  activeApiUrl:'',//当前正在查看的api接口
  historyApiList: []//浏览记录 近期查看或操作的接口列表
}

const mutations = {
  setUserInfo(state,obj){
    state.userInfo = obj
  },
  setActiveHost(state, host) {
    state.activeHost = host
  },
  setActiveApiUrl(state, url) {
    state.activeApiUrl = url
  },
  clearActiveApiUrl(state, url) {
    state.activeApiUrl = ''
  },
  setHistoryApiList(state, {url,desc}) {
    if(url.length<2){
      return
    }   
    let tempArr = state.historyApiList.filter(v => v.url != url)
    tempArr.unshift({url,desc})
    state.historyApiList = tempArr
  },
  clearHistoryApiList(state) {
    state.historyApiList = []
  }
}
const actions = {
  Login({}, loginForm) {
    const userName = loginForm.userName.trim()
    return new Promise((resolve, reject) => {
      login({userName, passWord:loginForm.passWord}).then(({token}) => {
        setToken(token)
        resolve()
      }).catch(error => {
        reject(error)
      })
    })
  },
  GetUserInfo({ commit }) {
    return new Promise((resolve, reject) => {
      getInfo().then(userInfo => {
        commit('setUserInfo', userInfo)
        resolve(userInfo)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // 登出 只需清楚token就可以了，无需请求接口
  FedLogOut({ commit }) {
    return new Promise(resolve => {
      commit('setUserInfo', {})
      removeToken()
      Message({
        message: `正在退出`,
        type: 'warning'
      })
      setTimeout(() => {
        resolve()
      }, 1000);
    })
  },
  setStore_activeHost({commit},host){
    commit('setActiveHost', host)
  },
  setStore_activeApiUrl({commit},url){
    commit('setActiveApiUrl', url)
  },
  clearStore_activeApiUrl({commit}){
    commit('clearActiveApiUrl')
  },
  setStore_historyApiList({commit},{url,desc}){
    commit('setHistoryApiList', {url,desc})
  },
  clearStore_historyApiList({commit}){
    commit('clearHistoryApiList')
  },
}
const store = new Vuex.Store({
  state,
  mutations,
  actions,
  modules: {
    // user
  },
  getters
})

export default store



